@import url(https://fonts.googleapis.com/css2?family=Nunito:wght@200;300;400;600;700;800;900&display=swap);
body {
  margin: 0;
  font-family: "Nunito", sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

::-webkit-scrollbar {
  height: 0;
  width: 0;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.App {
  display: flex;
  flex-direction: column;
  align-items: center;
}
.List {
  list-style: none;
  display: flex;
  align-items: center;
  width: 50rem;
  margin: 0rem auto;
  border: 0.1rem solid rgba(0, 0, 0, 0.233);
  padding: 1rem;
  justify-content: space-between;
}

.List-header {
  display: flex;
  align-items: center;
}

.List-header h2 {
  color: rgb(37, 36, 36);
  font-size: 1rem;
}

.List-img {
  width: 4rem;
  height: 4rem;
  border-radius: 100%;
  margin-right: 0.5rem;
}

.List-note {
  width: 30%;
  text-align: left;
}

.AddToList {
  display: flex;
  flex-direction: column;
  width: 30rem;
  margin: 5rem auto;
}

.AddToList-input {
  padding: 0.5rem;
  font-size: 1rem;
  margin: 0.3rem 0rem;
}

.AddToList-btn {
  padding: 0.5rem;
  cursor: pointer;
  background-color: #0b5468;
  font-weight: 700;
  color: white;
  border: none;
}

@media only screen and (max-width: 768px) {
  html {
    font-size: 70%;
  }
  .App {
    margin: 0rem 1rem;
  }
  .List {
    list-style: none;
    display: flex;
    align-items: center;
    width: 90vw;
    margin: 0 0;
    border: 0.1rem solid rgba(0, 0, 0, 0.233);
    padding: 1rem;
    justify-content: space-between;
  }

  .List-header {
    display: flex;
    align-items: center;
  }

  .List-header h2 {
    color: rgb(37, 36, 36);
    font-size: 1rem;
  }

  .List-img {
    width: 4rem;
    height: 4rem;
    border-radius: 100%;
    margin-right: 0.5rem;
  }

  .List-note {
    width: 50%;
    text-align: right;
  }

  .AddToList {
    display: flex;
    flex-direction: column;
    width: 30rem;
    margin: 5rem auto;
  }

  .AddToList-input {
    padding: 0.5rem;
    font-size: 1rem;
    margin: 0.3rem 0rem;
  }

  .AddToList-btn {
    padding: 0.5rem;
    cursor: pointer;
    background-color: #0b5468;
    font-weight: 700;
    color: white;
    border: none;
  }
}

